import React, {useState, useEffect} from 'react';
import {Navbar,Nav} from 'react-bootstrap';
/*Bootstrap styles */
import 'bootstrap/dist/css/bootstrap.min.css';


export const HeaderNav = () => {

  const [mode, setMode] = useState('dev')
  const [baseUrl, setBaseUrl] = useState(window.location.origin)

  useEffect(() => {

    if(process.env.REACT_APP_MODE){
      setMode(process.env.REACT_APP_MODE)
    }

    if(mode === 'prod'){
      setBaseUrl(process.env.BASE_URL)
    }

    console.log("mode: " + mode)
    console.log("base url: " + baseUrl)
    console.log(process.env.NODE_ENV)
    console.log(window.location)
  },[]);

  return (
          <Navbar collapseOnSelect expand="lg" bg="none" variant='dark'>
                {/* <Navbar.Brand href="/home"><span>{"<s1/>"}</span></Navbar.Brand> */}
                <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                <Navbar.Collapse id="responsive-navbar-nav">
                 <Nav className="justify-content-end" style={{ width: "100%" }}>
                  {/*ver si puedo cambiar este nav link por el de react para que el cambio sea mas lindo que el href, sino buscar alguno hecho */}
                    <Nav.Link active={window.location.pathname === "/" || window.location.pathname === "/#/" || window.location.pathname === "/#/home" ? true : false } href={baseUrl + '/#/home'}>Home</Nav.Link>
                    <Nav.Link active={window.location.pathname === "/#/about" ? true : false} href={baseUrl + '/#/about'}>About</Nav.Link>
                    <Nav.Link active={window.location.pathname === "/#/skills" ? true : false}  href={baseUrl + '/#/skills'}>Skills</Nav.Link>
                    <Nav.Link active={window.location.pathname === "/#/contact" ? true : false} href={baseUrl + '/#/contact'}>Contact</Nav.Link>
                  </Nav>
                </Navbar.Collapse>
           </Navbar>
  )
}